import { T_TENANT } from '../interfaces';

/**
 * Get Tenant from Host name.
 *
 * @returns {T_TENANT}
 */
function getTenant(): T_TENANT {
  const ARRAY_TENANT = ['id', 'my', 'ph', 'sg', 'tw', 'th', 'vn'];
  const hostName = window.location.hostname;
  // Get last index to get the tenant
  const tenantArray = hostName.split('.').slice(-1);

  // When slicing is no data, return tenant ID (Development mode)
  if (tenantArray.length === 0) {
    return 'id';
  }

  // Uppercase the last tenant
  const tenant = tenantArray[0];
  // If the tenant ID is not same with ARRAY_TENANT then it is Development mode / tenant not registered
  if (ARRAY_TENANT.indexOf(tenant) === -1) {
    return 'id';
  }

  return tenant as T_TENANT;
}

export default getTenant;
